@import url(https://fonts.googleapis.com/css?family=Poppins:400,700);
@import url(https://fonts.googleapis.com/css?family=Signika:700,400,600);
body {
  margin: 0;
  padding: 0;
  font-family: 'Signika', 'Poppins', -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --red: #e74c3c;
  --red-dark: #c0392b;
  --green: #2ecc71;
  --green-dark: #27ae60;
  --teal: #1abc9c;
  --teal-dark: #16a085;
  --purple: #9b59b6;
  --purple-dark: #8e44ad;
  --clouds: #ecf0f1;
  --clouds-dark: #bdc3c7;
}

:root {
  --primary: var(--purple);
  --primary-dark: var(--purple-dark);
  --background: var(--teal-dark);
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.btn {
  font-family: 'Signika';
  font-weight: bold;
  background-color: white;
  border: none;
  border-bottom: 3px solid lightgray;
  border-radius: 4px;
  padding: 7px 12px;
  margin: 10px;
  cursor: pointer;
  transition: background-color 0.1s linear;
}

.btn:hover {
  background-color: var(--clouds);
}

.navbar {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  -webkit-align-items: center;
          align-items: center;
  color: white;
}

.game-board {
  width: 100%;
  height: 100vh;
  background-color: var(--background);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.card-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  max-width: calc(100% - 100px);
  width: 1200px;
}

@media only screen and (max-width: 1220px) {
  .card-container {
    width: 900px;
  }
}

@media only screen and (max-width: 350px) {
  .card-container {
    max-width: calc(100% - 50px);
  }
}

:root {
  --card-width: 120px;
  --card-height: 170px;
}

.card {
  box-sizing: border-box;
  width: 120px;
  width: var(--card-width);
  height: 170px;
  height: var(--card-height);
  background-color: white;
  border-radius: 10px;
  padding: 15px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: background-color 0.5s linear;
}

.disable {
  opacity: 0;
}

.disable .card {
  cursor: default;
}

@media only screen and (max-width: 950px) {
  :root {
    --card-width: 80px;
    --card-height: 120px;
  }
  .card {
    font-size: 0.8em;
    padding: 10px;
  }
}

@media only screen and (max-width: 600px) {
  :root {
    --card-width: 40px;
    --card-height: 60px;
  }
  .card {
    padding: 5px;
    border-radius: 5px;
    font-size: 0.4em;
  }
}

.card-back {
  box-shadow: 00px 00px 50px var(--primary-dark) inset;
  width: 100%;
  height: 100%;
  background-color: var(--clouds);
}

.card-back img {
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
}

.card-front {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 100%;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.suit {
  color: var(--primary-dark);
  font-size: 1.5em;
}

/* entire container, keeps perspective */
.flip-container {
  -webkit-perspective: 1000px;
          perspective: 1000px;
  transition: opacity 0.3s linear, -webkit-perspective 0.5s ease;
  transition: perspective 0.5s ease, opacity 0.3s linear;
  transition: perspective 0.5s ease, opacity 0.3s linear, -webkit-perspective 0.5s ease;
}

.flip-container .flipper {
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}
/* flip the pane when hovered */
.flip-container.flip-open .flipper {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.flip-container,
.flip-back,
.flip-front {
  width: 120px;
  width: var(--card-width);
  height: 170px;
  height: var(--card-height);
}

/* flip speed goes here */
.flipper {
  transition: 0.6s ease-out;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;

  position: relative;
}

/* hide back of pane during swap */
.flip-back,
.flip-front {
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;

  position: absolute;
  top: 0;
  left: 0;
}

/* front pane, placed above back */
.flip-back {
  z-index: 2;
  /* for firefox 31 */
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
}

/* back, initially hidden pane */
.flip-front {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

